"use client"

import { useState } from "react"
import Link from "next/link"
import { motion, AnimatePresence } from "framer-motion"

import { Container } from "@/components/layout/container"
import { Button } from "@/components/ui/button"
import { Card, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { projects } from "@/data/projects"

const categories = ["All", "Web", "Mobile", "Design", "Strategy"]

export function Work() {
    const [filter, setFilter] = useState("All")

    const filteredProjects = projects.filter(
        (project) => filter === "All" || project.category === filter
    )

    return (
        <section id="work" className="py-12 md:py-24">
            <Container>
                <div className="mb-12 text-center">
                    <h2 className="text-4xl font-extrabold tracking-tight lg:text-5xl mb-4">Work</h2>
                    <p className="text-muted-foreground text-lg max-w-2xl mx-auto">
                        A selection of projects exploring the intersection of design and technology.
                    </p>
                </div>

                {/* Filter */}
                <div className="flex flex-wrap justify-center gap-2 mb-12">
                    {categories.map((category) => (
                        <Button
                            key={category}
                            variant={filter === category ? "default" : "outline"}
                            onClick={() => setFilter(category)}
                            className="rounded-full"
                        >
                            {category}
                        </Button>
                    ))}
                </div>

                {/* Grid */}
                <motion.div
                    layout
                    className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3"
                >
                    <AnimatePresence>
                        {filteredProjects.map((project) => (
                            <motion.div
                                layout
                                initial={{ opacity: 0, scale: 0.9, y: 20 }}
                                whileInView={{ opacity: 1, scale: 1, y: 0 }}
                                exit={{ opacity: 0, scale: 0.9, y: 20 }}
                                viewport={{ once: false, margin: "-50px" }}
                                transition={{ duration: 0.4 }}
                                key={project.id}
                            >
                                <Link href={project.link}>
                                    <Card className="h-full overflow-hidden transition-all hover:border-accent/50 hover:shadow-lg group">
                                        <div className="aspect-video w-full bg-muted/50 relative overflow-hidden group-hover:bg-muted/80 transition-colors">
                                            <div className="absolute inset-0 flex items-center justify-center text-muted-foreground/20 text-4xl font-bold uppercase tracking-widest group-hover:scale-105 transition-transform duration-500">
                                                {project.title}
                                            </div>
                                        </div>
                                        <CardHeader>
                                            <div className="flex items-center justify-between">
                                                <CardTitle>{project.title}</CardTitle>
                                                <span className="text-xs text-muted-foreground border px-2 py-0.5 rounded-full">{project.category}</span>
                                            </div>
                                            <CardDescription>{project.description}</CardDescription>
                                        </CardHeader>
                                        <CardFooter className="gap-2 pt-0">
                                            <div className="flex flex-wrap gap-2">
                                                {project.tags.slice(0, 3).map(tag => (
                                                    <span key={tag} className="text-xs text-muted-foreground bg-secondary/50 px-2 py-1 rounded-sm">{tag}</span>
                                                ))}
                                            </div>
                                        </CardFooter>
                                    </Card>
                                </Link>
                            </motion.div>
                        ))}
                    </AnimatePresence>
                </motion.div>
            </Container>
        </section>
    )
}
